function sliders() {
  // if (window.innerWidth <= 576) {
  //   const awardsSwiper = new Swiper(".awards__slider", {
  //     slidesPerView: 1,
  //     spaceBetween: "8%",
  //   });
  // }

  if (window.innerWidth <= 768) {
    const forumSwiper = new Swiper(".forum__swiper", {
      slidesPerView: 1,
      spaceBetween: "8%",
    });

    const newsSwiper = new Swiper(".news__swiper", {
      slidesPerView: 1,
      spaceBetween: "8%",
      on: {
        init: function () {
          const slides = document.querySelectorAll(".news__item");
          slides.forEach((slide) => {
            const btn = slide.querySelector(".news__btn"),
                  text = slide.querySelector(".news__text");

            btn.addEventListener("click", () => {
              text.classList.toggle('show');
              btn.classList.toggle('show');
              if (text.classList.contains('show')) {
                gsap.to(text, {height: 'auto', duration: 1, ease: "power3.out"})
              } else {
                gsap.to(text, {height: 0, duration: 1, ease: "power3.out"});
                gsap.to(window, { duration: 1, ease: "power3.out", scrollTo: '#news'});
              }
            });
          });
        },
        slideChange: function () {
          const currentBtn = this.slides[this.previousIndex].querySelector('.news__btn')
          if (currentBtn.classList.contains('show')) {
            currentBtn.click();
          }
        }
      },
    });
  }

  if(window.innerWidth >= 992) {
    const speakersSwiper = new Swiper('.speakers__swiper', {
      slidesPerView: 4,
      spaceBetween: '1%',
      navigation: {
        nextEl: ".speakers__next",
        prevEl: ".speakers__prev",
      },
    });
  }

  if (window.innerWidth < 992 && window.innerWidth >=769) {
    const speakersSwiper = new Swiper('.speakers__swiper', {
      slidesPerView: 3,
      spaceBetween: '1%',
      navigation: {
        nextEl: ".speakers__next",
        prevEl: ".speakers__prev",
      },
    });
  }
  
  if (window.innerWidth < 769 && window.innerWidth >=577) {
    const speakersSwiper = new Swiper('.speakers__swiper', {
      slidesPerView: 2,
      spaceBetween: '1%',
      navigation: {
        nextEl: ".speakers__next",
        prevEl: ".speakers__prev",
      },
    });
  }

  if (window.innerWidth <=576) {
    const speakersSwiper = new Swiper('.speakers__swiper', {
      slidesPerView: 1,
      spaceBetween: '8%',
    });
  }

  const gallerySwiper = new Swiper('.gallery__swiper', {
    slidesPerView: 1,
    spaceBetween: 20,
    loop: true,
    spaceBetween: 20,
    initialSlide: 1,
    navigation: {
      nextEl: ".gallery__next",
      prevEl: ".gallery__prev",
    },
    breakpoints: {
      991: {
        slidesPerView: 3,
      },
      576: {
        slidesPerView: 2,
      }
    }
  })

}

module.exports = sliders;
