function modal() {
  const buyBtn = document.querySelectorAll(".tickets__btn");
  const owerlay = document.querySelector(".owerlay");
  const body = document.querySelector("body");
  const closeBtn = document.querySelector(".modal__close");
  const modal = document.querySelector(".modal");

  let toggleBlock = gsap.to(modal, {
    height: "auto",
    paused: true,
    autoAlpha: 1,
    duration: 1,
    ease: "power1.out",
    onComplete: () => {
      gsap.set(modal, { overflowY: "auto" });
    },
  });

  buyBtn.forEach((btn) => {
    btn.addEventListener("click", () => {
      body.classList.add("lock");
      owerlay.classList.add("active");
      modal.classList.add("active");
      modal.classList.contains("active") ? toggleBlock.play() : toggleBlock.reverse();
    });
  })

  owerlay.addEventListener("click", () => {
    closeModal()
  });
  closeBtn.addEventListener("click", () => {
    closeModal()
  });

  function closeModal() {
    gsap.set(modal, { overflowY: "hidden" });
    body.classList.remove("lock");
    owerlay.classList.remove("active");
    modal.classList.remove("active");
    modal.classList.contains("active") ? toggleBlock.play() : toggleBlock.reverse();
  }

  const form = document.querySelector(".modal__form"),
    price = form.querySelector(".modal__price span"),
    discount = form.querySelector('.modal__input[name="discount"]');

  form.onchange = (e) => {
    if (e.target.name === "ticket-type") {
      price.textContent = e.target.value;
      discount.disabled = e.target.value == 0 ? true : false;
    }
  };
}

module.exports = modal;
