"use strict";
import timer from "./modules/timer";
import mobileMenu from "./modules/mobile-menu";
import sliders from "./modules/sliders";
import links from "./modules/links";
import tabs from "./modules/tabs";
import toggleContent from "./modules/toggle-content";
import modal from "./modules/modal";

window.addEventListener("load", () => {
  gsap.registerPlugin(ScrollToPlugin);
  gsap.registerPlugin(ScrollTrigger);

  mobileMenu();
  timer();
  sliders();
  links();
  tabs();
  toggleContent("#first-btn", "#first-content");
  toggleContent("#second-btn", "#second-content");
  // modal();
  scroll();

  const itemGet = document.querySelector(".awards__item--get");
  const promoButton = document.querySelector(".promo__button");

  itemGet.addEventListener("click", () => {
    gsap.to(window, { duration: 1, ease: "power1.out", scrollTo: "#tickets" });
  });

  promoButton.addEventListener("click", () => {
    gsap.to(window, { duration: 2, ease: "power1.out", scrollTo: "#tickets" });
  });
});

