function links() {
    const links = document.querySelectorAll('.speakers__img a');
    const arrows = document.querySelectorAll('.speakers__wrapper');

    function removeActive () {
        arrows.forEach(item => {
            item.classList.remove('active');
        })
    }

    function addActive (i) {
        arrows[i].classList.add('active');
    }

    // removeActive();

    links.forEach((item, i) => {
        item.addEventListener('mouseover', () => {
            addActive(i)
        });
    });

    links.forEach((item) => {
        item.addEventListener('mouseout', () => {
            removeActive();
        });
    });
}

module.exports = links;